


















































































































@import "~@/styles/variables";
@import "~@/styles/mixins";

.box {
  // position: relative;
}
.left-arrow {
  display: flex;
  align-items: center;
  position: absolute;
  padding-left: 2%;
  padding-right: 20%;
  top: 0;
  left: 0;
  height: 100%;
}
.right-arrow {
  display: flex;
  align-items: center;
  position: absolute;
  padding-left: 20%;
  padding-right: 2%;
  top: 0;
  right: 0;
  height: 100%;
}
.not-selected {
  transform: scale(0.9, 0.9);
  opacity: 0.6;
}
.slide {
  text-align: center;
}
.outfit-img {
  display: flex;
  position: relative;
  padding: 10px;
}
.img-absolute{
  visibility: hidden;
  margin: 0 auto;
  height: 100%;
  width: auto;
  max-height: 300px;
  @include breakpoint("small") {
    max-height: 400px;
  }
}
.img-fluid {
  filter: drop-shadow(0px 4px 2px rgba(0, 0, 0, 0.5));
  // display: inline;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  height: 100%;
  width: auto;
  max-height: 300px;
  @include breakpoint("small") {
    max-height: 400px;
  }

}
.max-width{ 
  max-width: 100vw;
}

