





































































































































































@import "~@/styles/variables";
@import "~@/styles/mixins";

span {
  vertical-align: baseline;
  border-radius: 10px;
  padding: 2px
}
.school-result {
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: minmax(60px,1fr) 3fr;
  align-items: center;
}
.school-logo-max {
  max-width: 90px;
}
.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
section {
  grid-row: 2;
  border-radius: 10px;
  margin: 20px 5px 10px 5px;
}
header {
  grid-row: 1;
}
a {
  cursor: pointer;
  color: $sky-blue;
  text-decoration: underline;
}
.right {
  background-color: lighten($green, 45%);
}
.wrong {
  background-color: lighten($red, 25%);
}
